import React from 'react'
import PropTypes from 'prop-types'
import Footer from '../components/Footer'
import { Navbar } from '../components/Navbar'
import './MainLayout.scss'
import LanguageSwitcher from '../components/LanguageSwitcher';
import locales  from '../locales/locales'
import { NavbarData } from './NavbarData'
import browserLang from 'browser-lang';
import { navigate } from 'gatsby';

export default function MainLayout({ children, language, location }) {

  const defaultLanguage = 'en';

  if(language !== 'fr' && language !== 'en') {
    language = defaultLanguage; // TODO: proper default language stored somewhere
  }

  const supportedLanguages = Object.keys(locales);

  // Skip build, Browsers only
  if (typeof window !== 'undefined')
  {

    if(language === defaultLanguage)
    {
      const userPreferedLanguage =
        window.localStorage.getItem('language') ||
        browserLang({
          languages: supportedLanguages,
          fallback: defaultLanguage,
        })

      if(userPreferedLanguage !== defaultLanguage)
      {
        const newUrl = `/${userPreferedLanguage.toLocaleLowerCase()}${location.pathname}`
        window.localStorage.setItem('language', userPreferedLanguage)
        navigate(newUrl);
      }
    }
  }

  return (
    <>
      <div className="CssGridNavContentFooter">
        <nav className="gridNavBar">
          <Navbar links={NavbarData[language].links}/>
        </nav>
        <div className="gridContent styleContent">
          {children}
        </div>
        <div className="gridFooter">
          <Footer/>
          {location && <LanguageSwitcher currentLanguage={language} availableLanguages={Object.keys(locales)} currentUrl={location.pathname}/>}
        </div>
      </div>
    </>
  );
}

MainLayout.propTypes = {
  children: PropTypes.node.isRequired,
  language: PropTypes.string,
  location: PropTypes.object
}
